/* LAYOUT STRUCTURE /*

/* sections */
.page-wrapper {
  display: grid;
  grid-template-rows: repeat(auto-fill, minmax(max-content, max-content));
  overflow-y: scroll;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory; }
  .page-wrapper .grid-item {
    min-height: 100vh;
    padding: 5rem 3rem;
    scroll-snap-align: start; }
    .page-wrapper .grid-item:nth-of-type(3n+1) {
      background-color: palegreen; }
    .page-wrapper .grid-item:nth-of-type(3n+2) {
      background-color: palegoldenrod; }
    .page-wrapper .grid-item:nth-of-type(3n+3) {
      background-color: paleturquoise; }
